#shortcuts-legend {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: var(--spacing-8);
  margin-top: var(--spacing-24);
  color: var(--color-bluegrey-700);
  font-size: 14px;
  border: 1px solid var(--color-bluegrey-900);
  border-radius: 3px;
}

#shortcuts-legend .shortcuts-group-items {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
