/*https://tobiasahlin.com/blog/layered-smooth-box-shadows/*/
/*https://sassme.jim-nielsen.com/*/

@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:100,100i,300,300i');

:root {
  --font-primary: 'Roboto Mono', sans-serif;
}

/*cadetblue, chartreuse*/

:root {
  --color-f913ad: #f913ad;
  --color-f95f13: #f95f13;
  --color-f81976: #f81976;

  --color-t1: rgba(255, 0, 0, 0);
  --color-t2: rgba(255, 0, 0, 1);

  --color-primary: #f913ad;
  --color-secondary: var(--color-f81976);
  --color-tertiary: #f81976;

  --color-grey-50: #fafafa;
  --color-grey-100: #f5f5f5;
  --color-grey-200: #eeeeee;
  --color-grey-300: #e0e0e0;
  --color-grey-400: #bdbdbd;
  --color-grey-500: #9e9e9e;
  --color-grey-600: #757575;
  --color-grey-700: #616161;
  --color-grey-800: #424242;
  --color-grey-900: #212121;

  --color-bluegrey-50: #eceff1;
  --color-bluegrey-100: #cfd8dc;
  --color-bluegrey-200: #b0bec5;
  --color-bluegrey-300: #90a4ae;
  --color-bluegrey-400: #78909c;
  --color-bluegrey-500: #607d8b;
  --color-bluegrey-600: #546e7a;
  --color-bluegrey-700: #455a64;
  --color-bluegrey-800: #37474f;
  --color-bluegrey-900: #263238;

  --color-light-grey: #22202b;
  --color-mid-grey: #333333;
  --color-dark-grey: #1b1a22;

  --color-success: #5ab400;
  --color-alert: #f81976;
  --color-warning: #ff9800;
  --color-info: #007cb0;
}

:root {
  --spacing-sm: 0.94rem;
  --spacing-md: 1.88rem;
  --spacing-lg: 2.82rem;

  --spacing-4: 4px;
  --spacing-8: 8px;
  --spacing-12: 12px;
  --spacing-16: 16px;
  --spacing-20: 20px;
  --spacing-24: 24px;
  --spacing-32: 32px;
  --spacing-36: 36px;
  --spacing-40: 40px;
  --spacing-44: 44px;
  --spacing-48: 48px;
  --spacing-54: 54px;
  --spacing-60: 60px;
  --spacing-72: 72px;
  --spacing-96: 96px;
}

:root {
  --header-height: 136px;
  --track-gap: 32px;
}

* {
  box-sizing: border-box;
}

html,
body {
  /*scroll-padding-top: calc(var(--header-height) + var(--track-gap));*/
}

html,
body,
#root {
  min-height: 100vh;
}

html {
  background: var(--color-grey-900);
}

body {
  margin: 0;
  color: #fff;
  font-family: var(--font-primary);
  font-weight: 100;
  line-height: 1;
}

a {
  outline: none;
  cursor: pointer;
  color: var(--color-grey-500);
  text-decoration: none;
  letter-spacing: 0.1em;
}

a:hover {
  text-decoration: none;
}

.hidden {
  display: none;
}

.me {
  display: grid;
  grid-template-rows: auto 80px;
  height: 100%;
  min-height: 100vh;
  overflow: visible;
}
