.tb-button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--color-bluegrey-600);
  line-height: 0;
  background-color: transparent;
  border: 1px solid var(--color-bluegrey-800);
  border-radius: 3px;
  transition: all 0.25s;
  cursor: pointer;
}

.tb-button.text {
  border-color: transparent;
  line-height: inherit;
}

.tb-button.text:hover {
  border-color: transparent;
  color: white;
}

.tb-button:hover {
  border-color: white;
}

.tb-button.size-24 {
  width: 24px;
  height: 24px;
}

.tb-button.size-28 {
  width: 28px;
  height: 28px;
}

.tb-button.size-32 {
  width: 32px;
  height: 32px;
}

.tb-button.size-36 {
  width: 36px;
  height: 36px;
}

.tb-button.size-40 {
  width: 40px;
  height: 40px;
}

.tb-button.size-44 {
  width: 44px;
  height: 44px;
}

.tb-button.size-48 {
  width: 48px;
  height: 48px;
}

.tb-button.circle {
  border-radius: 100%;
}

.tb-button.rounded {
  border-radius: 4px;
  width: auto;
  padding: 0 1em;
}

.tb-button:focus {
  outline: none;
  border-color: transparent;
  box-shadow: 0 0 0 3px white;
}

.tb-button.disabled {
  pointer-events: none;
}

.tb-button.active {
  background-color: var(--color-success);
  border-color: rgba(255, 255, 255, 0.75);
}

.tb-button.alert {
  background-color: var(--color-alert);
  border-color: rgba(255, 255, 255, 0.75);
}

.tb-button.warning {
  background-color: var(--color-warning);
  border-color: rgba(255, 255, 255, 0.75);
}

.tb-button.info {
  background-color: var(--color-info);
  border-color: rgba(255, 255, 255, 0.75);
}

.tb-button svg {
  pointer-events: none;
}

.tb-button.rotated {
  transform: rotate(180deg);
}
